.main-nav__toggle {
  display: none;
  position: absolute;
  right: 10px;
  top: 30px;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (max-width: 765px) {
  .main-nav__toggle--js {
    display: block;
  }
}
.main-nav__toggle--closed-js::before {
  position: absolute;
  content: "";
  top: 13px;
  left: 57%;
  transform: translateX(-50%);
  width: 23px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 7px 0 0 #ffffff, 0 14px 0 0 #ffffff;
}
.main-nav__toggle--open-js::after,
.main-nav__toggle--open-js::before {
  content: "";
  position: absolute;
  left: 5px;
  background-color: #000000;
  transform: rotate(45deg);
  height: 2px;
  width: 28px;
}
.main-nav__toggle--open-js::after {
  transform: rotate(-45deg);
}
