.contacts__icon {
  position: relative;
  padding-left: 28px;
  margin-bottom: 20px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .contacts__icon:last-child {
    margin-bottom: 7px;
  }
}
.contacts__icon::before {
  position: absolute;
  top: 2px;
  left: -2px;
  z-index: 1;
  content: "";
  width: 22px;
  height: 22px;
  background-position: center;
  background-repeat: no-repeat;
}
.contacts__icon--location::before {
  background-image: url("../img/icon-svg/location.svg");
}
.contacts__icon--phone::before {
  background-image: url("../img/icon-svg/phone.svg");
}
@media (min-width: 1366px) {
  .contacts__icon br {
    display: none;
  }
}
